* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Arial", sans-serif;
  background-color: #f4f4f4;
  color: #333;
}

.App {
  text-align: center;
}

.header {
  background-color: #282c34;
  padding: 50px 0;
  color: white;
}

.header-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.about-section, .projects-section, .contact-section {
  padding: 50px 0;
}

.projects {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.project-card {
  background: #fff;
  padding: 20px;
  margin: 10px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 250px;
  transition: all 0.3s ease;
}

.project-card h3 {
  margin-bottom: 10px;
}

footer {
  background-color: #282c34;
  color: white;
  padding: 20px 0;
}
